.nav h1 {
    margin-left: 30px;
    line-height: 25px;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 30px;
    font-weight: 900;
}
.nav-link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-right: 30px;
    -webkit-transition: .3s all ease-in-out;
    transition: .3s all ease-in-out;
    -webkit-transition-delay: .05s;
            transition-delay: .05s;
}
.nav-toggle {
    display: none;
    padding: 5px;
    margin-right: 20px;
}
.nav-bar {
    position: relative;
    width: 33px;
    height: 20px;
    cursor: pointer;
}
.nav-toggle span {
    width: 100%;
    height: 3px;
    background: #000 !important;
    display: block;
    top: 0;
}
.nav-toggle span:before, .nav-toggle span:after {
    content: '';
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background: #000;
}
.nav-toggle span:before {
    top: 45%;
}
.nav-toggle span:after {
    bottom: 0;
}
.nav-link a {
    padding: 5px;
    margin-right: 30px;
    color: #000;
    opacity: .75;
    font-size: 15px;
    font-weight: 900;
}
.nav-link a:last-of-type {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.nav-link a:hover {
    color: rgba(33, 181, 111, 1)
}
.nav-link a:hover svg {
    fill:rgba(33, 181, 111, 1)
    /* fill: inherit; */
}
.nav-link a svg {
    margin-right: 5px;
}
